<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body row p-0">
        <div class="col-sm-12">
          <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
        </div>
        <div class="col-sm-12">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="contact" :actions="actions" :actionsTable="actionsTable"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { Message, MessageBox } from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import contactSchemas from './contact-schemas'
  import dtHelper from 'src/helpers/datatable'

  export default{
    components: {
      MyTable,
      MyFilter
    },
    computed: {
      contact() {
        let rows = this.$store.state.contact;
        return dtHelper.filterByRules(rows, this.filterOutput);
      }
    },
    data () {
      return {
        filterOutput: [],
        columnDefs: dtHelper.buildColumDefs(contactSchemas),
        filter: {},
        rules:dtHelper.buildRules(contactSchemas),
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          },
        ],
        actionsTable: [
          {
            title: 'Chưa phản hồi',
            callback: this.unreplyAll
          },
          {
            title: 'Đã phản hồi',
            callback: this.replyAll
          },
          {
            title: 'Xóa',
            color: 'text-danger',
            callback: this.deleteAll
          }
        ],
      }
    },
    mounted() {
      window.AP = this;
      this.$store.dispatch('fetchContact');

      this.$store.dispatch('setPageTitle', 'liên hệ');
    },
    methods: {
      edit(index, row){
        this.$router.push('/contact/' + row.id);
      },
      updateFilter(filterOutput) {
        this.filterOutput = filterOutput;
      },
      readAll(rows){
        this.updateStatus(rows, 'read','read');
      },
      replyAll(rows){
        this.updateStatus(rows, 'reply', 'reply');
      },
      unreplyAll(rows){
        this.updateStatus(rows, 'reply', 'unreply');
      },
      deleteAll(rows){
        MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          this.updateStatus(rows, 'display', 'delete');
        })
      },
      updateStatus(rows, statusType, status){
        let self = this;
        let arrID = [];
        for (let row of rows) {
          arrID.push(row.id);
        }

        let data = {
          type_status: statusType,
          arrId: arrID,
          status: status
        }
        this.$store.dispatch('updateStatusContact', data).then(function (result) {
          self.$store.dispatch('fetchContact');
          Message({
            type: 'success',
            message: 'Cập nhật thành công'
          });
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      }
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
